<script>

import { Bar } from 'vue-chartjs'

export default {
  extends: Bar,
  data () {
    return {
      datacollection: {
        // Data to be represented on x-axis
        labels: [
          '06/يونيو',
          '07/يونيو',
          '08/يونيو',
          '09/يونيو',
          '10/يونيو',
          '11/يونيو',
          '12/يونيو',
          '13/يونيو',
          '14/يونيو',
          '15/يونيو',
          '16/يونيو',
          '17/يونيو',
          '18/يونيو',
          '19/يونيو'
        ],
        datasets: [{
          label: 'حالات مؤكدة',
          // backgroundColor: 'transparent',
          backgroundColor: 'red',
          // pointBackgroundColor: 'blue',
          // borderColor: 'blue',
          borderWidth: 3,
          // pointBorderColor: '#249EBF',
          fill: false,
          // Data to be represented on y-axis
          data: [
            80,
            73,
            78,
            135,
            71,
            29,
            73,
            82,
            101,
            92,
            46,
            66,
            77,
            539
          ]
        }
        ]
      },
      // Chart.js options that controls the appearance of the chart
      options: {
        datasets: { bar: { categoryPercentage: 0.9, barPercentage: 0.6 } },
        elements: { line: { fill: false },
          point: {
            radius: 0
          }},
        tooltips: {
          mode: 'index',
          intersect: false
        },
        hover: {
          bodyalign: 'right',
          mode: 'nearest',
          intersect: true
        },
        scales: {
          yAxes: [{
            display: true,
            stacked: true,
            scaleLabel: {
              display: true,
              labelString: 'عدد الحالات'
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [{
            // barPercentage: 0.6,
            // categoryPercentage: 1.2,
            display: true,
            stacked: true,
            scaleLabel: {
              display: false,
              labelString: 'التاريخ'
            },
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: false
        },
        responsive: true,
        maintainAspectRatio: false,
        layout: {
          padding: {
            left: 6,
            right: 6,
            top: 4,
            bottom: 6
          }
        }
      }
    }
  },
  mounted () {
    // renderChart function renders the chart with the datacollection and options object.
    this.renderChart(this.datacollection, this.options)
  }
}
</script>



