<template>
  <div id="provinces" class="svg-provinces"></div>
</template>

<script>

const d3 = require('d3')
const d3SvgLegend = require('d3-svg-legend')
const topojson = require('topojson')

export default {
  mounted: function () {
    var width = 900
    var height = 500

    var minimum = 0
    var maximum = 250
    // var state = 0

    var minimumColor = '#99ff99'
    var maximumColor = '#ff8080'

    // var allProvinces = d3.map()

    var colorScale = d3.scaleLinear()
      .domain([minimum, maximum])
      .range([minimumColor, maximumColor])

    var tooltip = d3.select('body').append('div')
      .attr('class', 'tooltip')
      .style('opacity', 0)

    var projection = d3.geoMercator()
      .center([ -7.0926, 31.7917 ])
      .scale(1400)
      .translate([450, 160])

    var svg = d3.select('#provinces')
      .append('svg')
      .attr('preserveAspectRatio', 'xMinYMin meet')
      .attr('viewBox', '0 0 ' + width + ' ' + height)
      .classed('svg-content', true)
      // .append('g')
      // .attr('transform', 'translate(' + 30 + ',' + 30 + ')')
    /*
    svg.append('text')
      .attr('x', (width / 1.3))
      .attr('y', 0)
      .attr('text-anchor', 'middle')
      .style('font-size', '16px')
      .style('text-decoration', 'underline')
      .text('الحالة الوبائية لفيروس كورونا باقاليم المغرب') */

    svg.append('text')
      .attr('x', 80)
      .attr('y', 40)
      .attr('text-anchor', 'middle')
      .style('font-size', '16px')
    // .style("text-decoration", "underline")
      .text('مناطق التخفيف')
    var path = d3.geoPath()
      .projection(projection)

    d3.queue()
      .defer(d3.json, 'static/data/provinces.json')
      .defer(d3.csv, 'static/data/provinces.csv')
      // eslint-disable-next-line handle-callback-err
      .await(function (error, provinces, data) {
        provinces = topojson.feature(provinces, provinces.objects.provinces)
        let mouseOver = function (d) {
          d3.select(this)
            .transition()
            .duration(200)
            .style('opacity', 1)
          // .style("stroke", "green")
          tooltip.transition()
          // .duration(200)
            .style('opacity', 1)
          tooltip.html('اقليم ' + '<strong>' + d.properties.NAME + '</strong>')
            .style('left', (d3.event.pageX) + 'px')
            .style('top', (d3.event.pageY - 28) + 'px')
        }

        let mouseLeave = function (d) {
          d3.select(this)
            .transition()
          // .duration(200)
            .style('opacity', 0.8)
          tooltip.transition()
            .duration(500)
            .style('opacity', 0)
        }
        // Provinces
        svg.append('g')
          .attr('class', 'province')
          .selectAll('path')
          .data(provinces.features)
          .enter()
          .append('path')
          .style('fill', function (d) {
            // d.total = allProvinces.get(d.properties.NAME) || 0
            // return colorScale(d.total)
            if (d.properties.NAME === 'العرائش' || d.properties.NAME === 'طنجة اصيلة' ||
            d.properties.NAME === 'العرائش' || d.properties.NAME === 'فاس' ||
            d.properties.NAME === 'الحاجب' || d.properties.NAME === 'الرباط' ||
            d.properties.NAME === 'سلا' || d.properties.NAME === 'الصخيرات تمارة' ||
            d.properties.NAME === 'الدار البيضاء' || d.properties.NAME === 'المحمدية' ||
            d.properties.NAME === 'القنيطرة' || d.properties.NAME === 'الجديدة' ||
            d.properties.NAME === 'النواصر' || d.properties.NAME === 'مديونة' ||
            d.properties.NAME === 'بنسليمان' || d.properties.NAME === 'برشيد' ||
            d.properties.NAME === 'مراكش'
            ) {
              return '#ff8080'
            } else {
              return '#99ff99'
            }
          })
          .attr('d', path)
          .attr('class', 'province-border')
          .attr('d', path)
          .attr('stroke', function (d) {
            if (d.properties.NAME === 'العرائش' || d.properties.NAME === 'طنجة اصيلة' ||
            d.properties.NAME === 'العرائش' || d.properties.NAME === 'فاس' ||
            d.properties.NAME === 'الحاجب' || d.properties.NAME === 'الرباط' ||
            d.properties.NAME === 'سلا' || d.properties.NAME === 'الصخيرات تمارة' ||
            d.properties.NAME === 'الدار البيضاء' || d.properties.NAME === 'المحمدية' ||
            d.properties.NAME === 'القنيطرة' || d.properties.NAME === 'الجديدة' ||
            d.properties.NAME === 'النواصر' || d.properties.NAME === 'مديونة' ||
            d.properties.NAME === 'بنسليمان' || d.properties.NAME === 'برشيد' ||
            d.properties.NAME === 'مراكش'
            ) {
              return '#ff0000'
            } else {
              return '#008000'
            }
          })
          .attr('stroke-width', function (d) {
            if (d.properties.NAME === 'العرائش' || d.properties.NAME === 'طنجة اصيلة' ||
            d.properties.NAME === 'العرائش' || d.properties.NAME === 'فاس' ||
            d.properties.NAME === 'الحاجب' || d.properties.NAME === 'الرباط' ||
            d.properties.NAME === 'سلا' || d.properties.NAME === 'الصخيرات تمارة' ||
            d.properties.NAME === 'الدار البيضاء' || d.properties.NAME === 'المحمدية' ||
            d.properties.NAME === 'القنيطرة' || d.properties.NAME === 'الجديدة' ||
            d.properties.NAME === 'النواصر' || d.properties.NAME === 'مديونة' ||
            d.properties.NAME === 'بنسليمان' || d.properties.NAME === 'برشيد' ||
            d.properties.NAME === 'مراكش'
            ) {
              return 0.8
            } else {
              return 0.5
            }
          })
          .attr('class', function (d) { return 'Province' })
          .style('opacity', 0.8)
          .on('mouseover', mouseOver)
          .on('mouseleave', mouseLeave)
      })
    // Legend
    svg = d3.select('svg')

    svg.append('g')
      .attr('class', 'legendLinear')
      .attr('transform', 'translate(30,60)')

    var legendLinear = d3SvgLegend.legendColor()
      .shapeWidth(30)
      // .title('التقسيم الوطني حسب الاقاليم')
      // .titleWidth(50)
      .labels(['م.1', 'م.2'])
      .cells(2)
      .labelWrap(30)
      // .shape('circle')
      .shapeWidth(15)
      .labelAlign('start')
      .shapePadding(5)
      .labelOffset(30)
      .orient('vertical')
      .scale(colorScale)

    svg.select('.legendLinear')
      .call(legendLinear)
  }
}
</script>

<style>
.svg-container {
    display: inline-block;
    position: relative;
    width: 100%;
    padding-bottom: 100%;
    vertical-align: top;
    overflow: hidden;
}
.svg-content {
    background: rgb(199, 216, 248);
    display: inline-block;
    position: relative;
    top: 0;
    left: 0;
}

path {
fill: rgb(2, 2, 2);
}
.province {
fill:  #FFA07A;
stroke:  rgb(250, 102, 43);
stroke-width: 0.5;
}
.selected {
fill: #FFA07A;
}
.contamination {
fill: #f31212;
stroke: #f31212;
stroke-width: 0.5;
}
circle {
fill: #CD5C5C;
}
circle.selected {
stroke: rgb(85, 85, 85);
stroke-width: 2px;
}
.incident {
fill: steelblue;
}
.sphere {
fill: #fff;
}
.land {
fill: #000;
}
div.tooltip {
  position: absolute;
  width: auto;
  height: auto;
  padding: 10px;
  background-color: rgb(250, 234, 234);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  -webkit-box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.4);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.4);
  pointer-events: none;
}
@media (min-width: 220px) and (max-width: 419px) {
  div.tooltip {
    width: 60px;
    height: auto;
    font-size: 10px;
  }
}
@media (min-width: 420px) and (max-width: 659px) {
  div.tooltip {
    width: 120px;
    height: auto;
    font-size: 10px;
  }
}

@media (min-width: 660px) and (max-width: 899px) {
  div.tooltip {
    width: 150px;
    height: auto;
    font-size: 15px;
  }
}

@media (min-width: 900px) {
  div.tooltip {
    width: auto;
    height: auto;
  }
}
</style>



